import React from "react"
import Layout from "../components/layout";
import { graphql, useStaticQuery, Link } from "gatsby";
import Helmet from 'react-helmet'

const BlogPage = () => {
  const data = useStaticQuery(graphql`query {
        allMarkdownRemark(sort: {fields: frontmatter___date, order: DESC}, limit: 10) {
          edges {
            node {
              frontmatter {
                title
                date(formatString: "MMMM Do, YYYY")
                slug
              }
              html
              excerpt
              fields {
                slug
              }
            }
          }
        }
      }`);

  return <Layout>
    <div className="posts">
      {data.allMarkdownRemark.edges.map((edge, index) => {
        const pattern = /\d{4}\-(0?[1-9]|1[012])\-(0?[1-9]|[12][0-9]|3[01])-/gm;
        const newslug = edge.node.fields.slug.match(pattern).join().replace(/\-/g, "/").slice(0, -1);
        return <div className="post" key={index}>
          <h1 className="post-title">
            <Link to={`archive/${newslug}/${edge.node.frontmatter.slug}`}>
              {edge.node.frontmatter.title}
            </Link>
          </h1>

          <span className="post-date">{edge.node.frontmatter.date}</span>

          {edge.node.excerpt}

        </div>
      })}
    </div>
    <Helmet>
      <title>Home | Sirwan Afifi</title>
    </Helmet>

    <div className="pagination">
      <Link to="/page/2"></Link>
      <Link to="/page/2">More</Link>
    </div>
  </Layout>
};

export default BlogPage;